<template>
  <div class="masthead">
      <div class="heading">
          Acme Dev Team
      </div>
      <div class="nav">
          <span class="nav-item nav-row-new" @click="onAddRow">+ Add row</span>
          <span class="nav-item nav-task-new action-task-new" @click="onAddCard">+ Add card</span>
      </div>
  </div>
</template>

<script>
import EventBus from './EventBus'

export default {
  name: 'masthead',
  methods: {
    onAddCard: function () {
      EventBus.$emit('masthead-add-card', true)
    },
    onAddRow: function () {
      EventBus.$emit('row-edit-details', 'new-row')
    }
  }
}
</script>

<style scoped>
  .masthead {
      width: 371px; /*248px; /* 299px; /* 229px if there is only 1 button */;
  }
  .heading {
      background-color: black;
      color: white;
      font-size: 26px;
      padding: 5px 8px 4px;
      font-weight: bold;
      display: inline-block;
      margin: 0px 0 15px;
  }
  .nav {
      padding-top: 2px;
      float: right;
  }
  .nav-item {
      background-color: #CCC; /* #209cee #ccc */
      padding: 4px 5px 4px 4px;
      font-size: 13px;
      cursor: pointer;
      margin-left: 3px;
      border-radius: 2px;
      color: #000;
  }
  .nav-item:hover {
      background-color: #BBB; /* #1190e3 bbb rgba(221, 221, 221, 0.7)*/
  }
</style>



